import { Context, Hono, Next } from "hono";
import Stripe from "stripe";

import { AccessTypeType } from "@sassy/db/schema-validators";

import type { AppType } from "../types";

// Initialize Hono app for stripe webhook routes
export const stripeWebhooksRouter = new Hono<AppType>();

// Stripe webhook verification middleware
const verifyStripeWebhook = async (c: Context, next: Next) => {
  const payload = await c.req.arrayBuffer();
  const signature = c.req.header("stripe-signature");

  if (!signature) {
    return c.json({ error: "Missing Stripe signature header" }, 400);
  }

  const webhookSecret = c.env.STRIPE_WEBHOOK_SECRET as string;
  const secretKey = c.env.STRIPE_SECRET_KEY as string;

  // Create Stripe instance directly in middleware
  const stripe = new Stripe(secretKey, { apiVersion: "2023-08-16" });

  // Verify the webhook
  let event: Stripe.Event;

  event = stripe.webhooks.constructEvent(
    Buffer.from(payload),
    signature,
    webhookSecret,
  );

  // Store event in context for the handler
  c.set("webhookEvent", event);
  await next();
};

// Determine access type based on subscription status and interval
// Convert to values that match your database schema's enum
const getAccessTypeFromStripeEvent = (
  subscription: Stripe.Subscription,
): AccessTypeType => {
  // Active subscription - check for yearly or monthly
  if (subscription.status === "active") {
    return subscription.items.data[0]?.plan?.interval === "year"
      ? "YEARLY"
      : "MONTHLY";
  }
  // Trial period
  else if (subscription.status === "trialing") {
    return "TRIAL";
  }
  // Any other status (paused, incomplete, etc.)
  else {
    return "FREE";
  }
};

// Stripe webhook handler
stripeWebhooksRouter.post("/stripe", verifyStripeWebhook, async (c) => {
  const event = c.get("webhookEvent") as Stripe.Event;
  console.log(`Stripe webhook event received: ${event.type}`);
  const subscription = event.data.object as Stripe.Subscription;
  const customer = subscription.customer as Stripe.Customer;
  const customerJson = JSON.parse(JSON.stringify(customer));
  const clerkUserId = subscription.metadata.clerkUserId;
  const accessType = getAccessTypeFromStripeEvent(subscription);

  // Get Prisma client from context
  const db = c.get("prisma");

  switch (event.type) {
    // Handle customer data using the clerkId stored in metadata
    case "customer.created":
    case "customer.updated": {
      const customer = event.data.object as Stripe.Customer;
      const clerkUserId = customer.metadata.clerkUserId;

      const customerJson = JSON.parse(JSON.stringify(customer));
      // Update user with Stripe customer ID and data
      await db.user.update({
        where: { id: clerkUserId },
        data: {
          stripeCustomerId: customer.id,
          stripeUserProperties: customerJson,
          accessType: accessType,
        },
      });
      console.log(`User updated with Stripe customer data: ${customer.id}`);
      break;
    }

    // Handle subscription events
    case "customer.subscription.created": {
      //new customer so no stripeCustomerId yet in the userDb
      //so we need to find the user by the clerkId stored in metadata
      const user = await db.user.update({
        where: { id: clerkUserId },
        data: {
          stripeCustomerId: customer.id,
          stripeUserProperties: customerJson,
          accessType: accessType,
        },
      });
      break;
    }

    case "customer.subscription.updated": {
      const accessType = getAccessTypeFromStripeEvent(subscription);

      await db.user.update({
        where: { id: clerkUserId },
        data: {
          stripeCustomerId: customer.id,
          accessType: accessType,
          stripeUserProperties: customerJson,
        },
      });

      console.log(
        `User subscription updated: ${subscription.id}, access type: ${accessType}`,
      );
      break;
    }

    case "customer.subscription.deleted": {
      await db.user.update({
        where: { id: clerkUserId },
        data: {
          accessType: "FREE",
          stripeCustomerId: null,
          stripeUserProperties: undefined,
        },
      });

      console.log(
        `User subscription deleted, access revoked for user: ${clerkUserId}`,
      );
      break;
    }

    default:
      console.log(`Ignored Stripe webhook event type: ${event.type}`);
  }

  return c.json({ success: true });
});
